import React, { Component } from 'react';
import Badge from "./Badge"
import DescriptiveHotHead from './DescriptiveBudget';
import DescriptiveExpense from './DescriptiveExpense';

const GraphTypes = {
    Budget: 'Budget',
    Expense: 'Expense'
    };

class DescriptiveMiddle extends Component{
    constructor(props){
      super(props);
      this.state = {
          type:""
      }
    }
  
    onSetGraphType = (e) => {
        this.setState({type: e.target.getAttribute("value")})
      }

    componentWillMount() {
        this.setState({type:"Budget"})
    }
    
    badgesSection = (graphTypes) => {
        return  <div className="d-flex flex-row justify-content-around mt-3">
        {graphTypes.map( (graphType, index) =>  <Badge onClick={this.onSetGraphType} graphType={graphType} key={index} />)}
      </div>
      }

    checkState = (state) => {
        let component = ""
        switch(state) {
            case "Expense":
                component = <DescriptiveExpense />
              break;
            case "Budget":
                component = <DescriptiveHotHead />
              break;
            default:
                component = <DescriptiveExpense />
          }
        return component
    }
    
    render(){
      return (<div>
                {this.badgesSection(Object.values(GraphTypes))}
                {this.checkState(this.state.type)}
      </div>
      )
    }
  }
  
  export default DescriptiveMiddle;