class DescriptiveExpense extends Component{
  constructor(props){
    super(props);
    this.state = {
      chartData: {},
      yAxesLabel: "",
      xAxesLabel: ""
    }
  }

  static defaultProps = {
    type: "รายจ่าย",
    displayTitle:true,
    displayLegend: true,
    legendPosition:'right',
    yAxesLabel: "Million baht",
    xAxesLabel: "Year-Month (ค.ศ.)"
  }

  componentWillMount() {
    this.setState({
      chartData: data,
      yAxesLabel: this.props.yAxesLabel,
      xAxesLabel: this.props.xAxesLabel
    })
  }
  
  render(){
    return (
      <div className="chart">
        <Line
          data={this.state.chartData}
          options={{
            title:{
              display:this.props.displayTitle,
              text: this.props.type,
              fontSize:25
            },
            legend:{
              display:this.props.displayLegend,
              position:this.props.legendPosition
            },
            scales: {
                xAxes: [{
                    ticks: {
                        beginAtZero:true
                    },
                    scaleLabel: {
                        display: true,
                        labelString: this.state.xAxesLabel,
                        fontSize: 20
                    }
                }],
                yAxes: [{
                  ticks: {
                      beginAtZero:true
                  },
                  scaleLabel: {
                      display: true,
                      labelString: this.state.yAxesLabel,
                      fontSize: 20
                  }
              }]
            }
          }}
        />
      </div>
    )
  }
}

export default DescriptiveExpense;