import React, { Component } from 'react';
import Badge from "./Badge"
import DescriptiveMiddle from './DescriptiveMiddle';
import TimeSeries from './TimeSeries';

const imgStyle = {
  margin: "0 auto"
}

const GraphTypes = {
    Descriptive: 'Descriptive',
    TimeSeries: 'Time-Series',
    SubmittedBy: 'Submitted By'
    };

class Descriptive extends Component{
    constructor(props){
      super(props);
      this.state = {
          type:""
      }
    }
  
    onSetGraphType = (e) => {
        this.setState({type: e.target.getAttribute("value")})
      }

    componentWillMount() {
        this.setState({type:"Descriptive"})
    }
    
    badgesSection = (graphTypes) => {
        return  <div className="d-flex flex-row justify-content-around mt-3">
        {graphTypes.map( (graphType, index) =>  <Badge onClick={this.onSetGraphType} graphType={graphType} key={index} />)}
      </div>
      }

    checkState = (state) => {
        let component = ""
        switch(state) {
            case "Descriptive":
                component = <DescriptiveMiddle />
              break;
            case "Time-Series":
                    component = <TimeSeries />
              break;
            case "Submitted By":
                    component = <div className="d-flex flex-column text-center mt-4">
                      <img  style={imgStyle}
                      height="100" width="s100"
                      alt="" src="https://lh3.googleusercontent.com/-uEk8PNW7Cfw/AAAAAAAAAAI/AAAAAAAAAAA/ACHi3rfG8tvEirt9t8beecWxLN1GcnqgoQ/mo/photo.jpg?sz=46"/>
                      <p>Wachirawit Kaewlomsup</p><p>5910505513</p><p>Computer Engineering</p></div>
            break;
            default:
                component = <DescriptiveMiddle />
          }
        return component
    }
    
    render(){
      return (<div>
                {this.badgesSection(Object.values(GraphTypes))}
                {this.checkState(this.state.type)}
      </div>
      )
    }
  }
  
  export default Descriptive;