import React, {Component} from 'react';
import { Bar} from 'react-chartjs-2';

const data = {
    labels: [
        2553,2554,2555,2556,2557,2558,2559,2560,2561,2562],
    datasets: [{
      label: 'งบประมาณกระทรวงอุตสาหกรรม',
      fill: false,
      data: [
        5876.965,7351.9734,8683.2227,7384.25733,7467.7227,5857.9019,5986.3722,5806.7681,5647.2799,5478.1637
      ],
      backgroundColor: [
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)",
        "rgb(146, 226, 253)"
      ],
      borderColor: "rgb(146, 226, 253)"
    }]
  }

class DescriptiveHotHead extends Component{
  constructor(props){
    super(props);
    this.state = {
      chartData: {},
      yAxesLabel: "",
      xAxesLabel: ""
    }
  }

  static defaultProps = {
    type: "งบประมาณ",
    displayTitle:true,
    displayLegend: true,
    legendPosition:'right',
    yAxesLabel: "Million baht",
    xAxesLabel: "Year(พ.ศ.)"
  }

  componentWillMount() {
    this.setState({
      chartData: data,
      yAxesLabel: this.props.yAxesLabel,
      xAxesLabel: this.props.xAxesLabel
    })
  }
  
  render(){
    return (
      <div className="chart">
        <Bar
          data={this.state.chartData}
          options={{
            title:{
              display:this.props.displayTitle,
              text: this.props.type,
              fontSize:25
            },
            legend:{
              display:this.props.displayLegend,
              position:this.props.legendPosition
            },
            scales: {
                xAxes: [{
                    ticks: {
                        beginAtZero:true
                    },
                    scaleLabel: {
                        display: true,
                        labelString: this.state.xAxesLabel,
                        fontSize: 20
                    }
                }],
                yAxes: [{
                  ticks: {
                      beginAtZero:true
                  },
                  scaleLabel: {
                      display: true,
                      labelString: this.state.yAxesLabel,
                      fontSize: 20
                  }
              }]
            }
          }}
        />
      </div>
    )
  }
}

export default DescriptiveHotHead;